const MAX_SAFE_INTEGER = Number.MAX_SAFE_INTEGER || Math.pow(2, 53) - 1;

const toNumberInput = v => {
  const num = Number(v);
  if (isNaN(num)) {
    return 0;
  }

  if (!/^(-?\d+)(\.\d+)?$/.test(num) || num.length > 16) {
    return 0;
  }

  return num;
};

const getValidValue = (value, min, max, toFixed) => {
  let val = Number(value);
  if (isNaN(val)) {
    val = 0;
  }

  val = Number(val.toFixed(toFixed));
  if (val < min) {
    val = min;
  }

  if (val > max) {
    val = max;
  }

  return val;
};
export default {
  name: 'lz-numinput',
  props: {
    /* 控件使用ID 相同组件useId都不可雷同 */
    useId: {
      type: String,
      default: 'lz-numinput'
    },

    value: {
      type: [Number, String],
      default: 0
    },

    min: {
      type: [Number, String],
      default: -MAX_SAFE_INTEGER
    },
    max: {
      type: [Number, String],
      default: MAX_SAFE_INTEGER
    },
    // 步长
    step: {
      type: [Number, String],
      default: 1
    },
    // 小数位
    toFixed: {
      type: [Number, String],
      default: 0
    },

    // 允许键盘输入
    inputEnabled: {
      type: Boolean,
      default: false
    },
    // 输入域 name 可以在父form 中获取值
    inputName: {
      type: String,
      default: 'numinput'
    },

    // 输入域 自定义  输入域 的样式
    inputStyle: {
      type: String,
      default: ''
    },
    // 加减 按钮 自定义 按钮 的样式
    btnStyle: {
      type: String,
      default: ''
    },
    // 按钮风格  1 - 实心；2 - 镂空
    btnTheme: {
      type: [String, Number],
      default: ''
    },

    // 皮肤样式  1 - 方形圆角 ；2 - 圆形；
    skin: {
      type: [String, Number],
      default: ''
    },
    // 当按钮不可操作时 - 是否隐藏按钮
    hideBtnDisabled: {
      type: Boolean,
      default: false
    },
    // 是否 隐藏初始值 0  - 一般用于 商品列表 快速向购物车添加商品
    hideNumZero: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      inputValue: 0,
      disabledMin: false,
      disabledMax: false,
      hasTap: false,
      allowInput: false
    };
  },
  computed: {
    if_show_num() {
      if (!!this.hideNumZero && this.inputValue == 0) {
        return false;
      }
      return true;
    },
    if_disabled_input() {
      return !(this.inputEnabled && this.allowInput);
    }
  },
  watch: {
    value(v, o_v) {
      if (v != o_v) {
        this.updated(v, true, true);
      }
    },
    min(v, o_v) {
      if (v != o_v) {
        this.updated(this.value, true, true);
      }
    },
    max(v, o_v) {
      if (v != o_v) {
        this.updated(this.value, true, true);
      }
    }
  },
  created() {
    this.init();
  },

  methods: {
    init() {
      this.updated(this.value, true, true);
    },
    /**
     * 更新值
     */
    updated(value, condition = true, callChange = false) {
      const min = this.min;
      const max = this.max;
      const old_value = this.inputValue;

      let toFixed = Number(this.toFixed);
      if (toFixed < 0) {
        toFixed = 0;
      }
      const inputValue = getValidValue(value, min, max, toFixed);
      const disabledMin = inputValue <= min;
      const disabledMax = inputValue >= max;

      // 更新数值，判断最小或最大值禁用 按钮
      if (condition) {
        this.inputValue = inputValue;
        this.disabledMin = disabledMin;
        this.disabledMax = disabledMax;
      }

      // 触发事件
      if (callChange) {
        this.emitCall('change', {
          newVal: inputValue,
          oldVal: old_value
        });
      }
    },
    /**
     * 数字计算函数
     */
    calculation(type) {
      const step = Number(this.step);
      const inputValue = Number(this.inputValue);
      if (type === 'up') {
        if (this.disabledMax) return false;
        this.updated(inputValue + step, true, true);
      }

      if (type === 'down') {
        if (this.disabledMin) return false;
        this.updated(inputValue - step, true, true);
      }
    },
    /**
     * 当键盘输入时
     */
    inputchange(e) {
      const value = toNumberInput(e.detail.value);
      this.allowInput = false;
      this.updated(value, true, true);
    },

    /* 长按 输入域 */
    inputlongtap(e) {
      if (!!this.inputEnabled) {
        this.allowInput = true;
      } else {
        this.allowInput = false;
      }
    },

    /* 点击 加减 按钮 */
    btntap(e) {
      const { type } = e.currentTarget.dataset;
      this.hasTap = true;
      this.allowInput = false;
      this.calculation(type);
    },
    emitCall(code, opts = {}) {
      switch (code) {
        case 'change':
          if (opts.newVal != opts.oldVal) {
            this.$emit(
              'change',
              {
                value: opts.newVal,
                useId: this.useId,
              }
            );
          }

          break;
      }
    }
  }
};
